/* =============================================================================================================*/
footer p {
  height: 30px !important;
}

#header {
  width: 100%;
  height: 80vh;
  margin-top: 50px;
}

#header h1 {
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}

#header .brand-name {
  color: #3498db;
}

#header h2 {
  color: #484848;
  font-size: 23px;
}

#header .btn-get-started {
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  -ms-border-radius: 50px;
  -o-border-radius: 50px;
  transition: 0.5s;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
  color: #3498db;
  border: 2px solid #3498db;
  text-decoration: none;
}

#header .btn-get-started:hover {
  background: #3498db;
  color: #fff;
}

.header-img {
  text-align: right;
}

#header .animated {
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
  -webkit-animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@keyframes up-down {
  0% {
    transform: translateY(10px);
    -webkit-transform: translateY(10px);
    -moz-transform: translateY(10px);
    -ms-transform: translateY(10px);
    -o-transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
    -webkit-transform: translateY(-10px);
    -moz-transform: translateY(-10px);
    -ms-transform: translateY(-10px);
    -o-transform: translateY(-10px);
  }
}

@media (max-width: 991px) {
  #header {
    height: 80vh;
    text-align: center;
  }
  #header .heading-img {
    text-align: center;
  }
  #header .header-img img {
    width: 100%;
  }
}

@media (max-width: 768px) {
  #header {
    margin-top: 20px;
  }
  #header h1 {
    font-size: 28px;
  }
  #header h2 {
    font-size: 18px;
    margin-bottom: 30px;
  }
  #header .header-img img {
    width: 100%;
  }
  .mt-4 {
    margin-bottom: 2.5rem !important;
  }
  .navbar a {
    font-size: 26px !important;
  }
  .navbar-toggler {
    font-size: 14px !important;
  }
  #navbarSupportedContent .nav-link {
    font-size: 19px !important;
    width: 80px;
  }

  .navbar-collapse .collapse {
    transition: height 0.5s ease !important;
    overflow: hidden !important;
    -webkit-transition: height 0.5s ease !important;
    -moz-transition: height 0.5s ease !important;
    -ms-transition: height 0.5s ease !important;
    -o-transition: height 0.5s ease !important;
    -webkit-transition: height 0.5s ease !important;
  }
}

